import { useEffect, useState } from "react";

import { songApi } from "src/api";
import { StyledHomePage, ListSongComponent } from "src/components";
import { convertSongObj } from "src/utils";

export const Home = () => {
    const [songs, setSongs] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    // effect function
    const fetchSongs = async () => {
        try {
            setIsLoading(true);
            const res = await songApi.search({ all: true });
            setSongs(res.data.songs.map(song => convertSongObj(song)));
        } catch (err) {
            console.log(err.response);
        } finally {
            setIsLoading(false);
        }
    };
    // effect function

    // effect
    useEffect(() => {
        fetchSongs();
    }, []);
    // effect

    return (
        <StyledHomePage>
            <div className="banner"></div>
            <ListSongComponent 
                heading="Bi ht c nghe nhiu nht"
                listSongs={songs}
                isFetching={isLoading}
            />
            <ListSongComponent 
                heading="Playlist c yu thch nht"
                listSongs={songs}
                isFetching={isLoading}
            />
            <ListSongComponent 
                heading="Bi ht nghe gn y"
                listSongs={songs}
                isFetching={isLoading}
            />
        </StyledHomePage>
    );
};